import search from '../../components/search'
import navBox from '../../components/navbox'
import goodslist from '../../components/goodslist'
export default {
  name: 'index',
  data () {
    return {
      goodslist: [],
      loadingstatus: false,
      page: 1,
      arrangement: 'h'
    }
  },
  mounted () {
    this.nextPage()
  },
  components: {
    navBox,
    search,
    goodslist
  },
  onReachBottom(){
	  this.pageChange()
  },
  methods: {
    pageChange () {
      this.page = this.page + 1
      this.nextPage()
    },
    loading (s) {
      this.loadingstatus = s
    },
    nextPage () {
		var that=this
		uni.request({
			url: getApp().globalData.APIurl+'/portal/index/getTbkMaterial',
			data: {
			    page: this.page,
			    material_id: 4092
			},
			header: {
			    'Token': that.token ,
				'Device-Type':'wxapp'
			},
			success: (res) => {
				if(res.data.code){
					that.loadingstatus = false
					var list = res.data.data.list
					that.goodslist = that.goodslist.concat(list)
				}
			}
		})
    }
  }
}
